/* nunito sans serief font family only for heading tag in website website */
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@800&display=swap');
/* poopins fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
* {
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
  font-family: var(--heading-font);
}
:root {
  --golden-color: rgb(255, 183, 0);
  --light-color:white;
  --gray-color:#646464 ;
  --heading-font:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  --content-font: 'Poppins', sans-serif;
}
.App {
  width: 100%;
  min-height: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.958);
}
html {
  scroll-behavior: smooth;
}
.heading {
  width: 100%;
  color: rgba(255, 255, 255, 0.3);
  text-align: center;
  font-size: 6cqmax;
  font-weight: bolder;
  margin-top: 3rem;
  margin-bottom: 2rem;
  position: relative;
  letter-spacing: 0.2rem;
}
.heading h1 {
  letter-spacing: 0rem;
  font-size: 3.5cqmax;
  color: var(--golden-color);
  position: absolute;
  text-shadow: 0px 0px 8px var(--golden-color);
}
.heading span {
  color: var(--light-color);
}
/* width */
::-webkit-scrollbar {
  width: 16px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(227, 226, 226);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--golden-color);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(242, 178, 16, 0.76);
} 

@media screen and (max-width:500px) {
  .heading {
    font-size: 6vmax;
    letter-spacing: 0rem;
  }
  .heading h1 {
    letter-spacing: 0rem;
    font-size: 4vmax;
  } 
}
